<template>
  <!--
      <MyHeader @addIt='addItem'/>
      组件自定义事件改：需要父组件与子组件中分别写方法，父组件中@xxx='function',子组件中this.$emit('xxx')，
      但是由于此版本我是直接从父到子传方法，所以只有MyHeader可以改写
   -->
  <div id="app">
    <div class="todo-container">
      <div class="todo-wrap">
        <h3 style="text-align: center">记事本</h3>
        <MyHeader/>
        <MyList :todos="todos"/>
        <MyFooter :todos="todos"/>
      </div>
    </div>
  </div>

</template>

<script>
// import pubsub from 'pubsub-js'
import MyHeader from "@/components/MyHeader";
import MyList from "@/components/MyList";
import MyFooter from "@/components/MyFooter";

export default {
  name: 'App',
  data() {
    return {
      todos: JSON.parse(localStorage.getItem('todos')) || []
    }
  },
  components: {
    MyHeader,
    MyList,
    MyFooter,
  },
  methods: {},

  watch: {

    todos: {
      deep: true,
      handler(newTodos) {
        const result = JSON.stringify(newTodos)
        localStorage.setItem('todos', result)
      }
    }

  },

  mounted() {

    //$on后直接写表达式，没有等号=
    this.$bus.$on('add', (todoObj) => {
      this.todos.unshift(todoObj)
    })

    this.$bus.$on('changeTo', (id) => {
      this.todos.forEach((todo) => {
        if (todo.id === id) {
          todo.done = !todo.done
        }
      })
    })

    this.$bus.$on('deleteIt', (id) => {
      if (confirm("确认删除吗")) {
        this.todos = this.todos.filter((todo) => {
          return todo.id !== id
        })
      }
    })

    this.$bus.$on('changeAll', (check) => {
      this.todos.forEach((todo) => {
        todo.done = check
      })
    })

    this.$bus.$on('clearDo', () => {
      if (confirm("确认清除已完成吗")) {
        this.todos = this.todos.filter((todo) => {
          return !todo.done
        })
      }
    })

    this.$bus.$on('handleBl', (id, title) => {
      this.todos.forEach((todo) => {
        if (todo.id === id) {
          todo.title = title
        }
      })
    })


  }


}
</script>

<style>

#app {
  margin: 30px
}

/*base*/
body {
  background: #fff;
}

.btn {
  display: inline-block;
  padding: 4px 12px;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
}

.btn-danger {
  color: #fff;
  background-color: #da4f49;
  border: 1px solid #bd362f;
}

.btn-danger:hover {
  color: #fff;
  background-color: #bd362f;
}

.btn-edit {
  color: #fff;
  background-color: skyblue;
  margin-right: 5px;
}

.btn:focus {
  outline: none;
}

.todo-container {
  width: 600px;
  margin: 0 auto;
}

.todo-container .todo-wrap {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
</style>