<template>
<!--
    transition标签包裹
 -->
<!--  <transition appear name="todo">-->
    <li>
      <div>
        <input type="checkbox"
               :checked="todo.done"
               @click="changeTo(todo.id)"
        />
        <span v-show="!todo.isEdit">{{todo.title}}</span>
        <input type="text"
               :value="todo.title"
               v-show="todo.isEdit"
               @keyup.enter="handleBlur(todo,$event)"
               @blur="handleBlur(todo,$event)"
               ref="inputTitle"
        >
        <button class="btn btn-danger" @click="deleteItem(todo.id)">删除</button>
        <button class="btn btn-edit" @click="editTodoItem(todo)" v-show="!todo.isEdit">编辑</button>
      </div>

    </li>
<!--  </transition>-->
</template>


<script>
    // import pubsub from 'pubsub-js'
    export default {
      name:'MyItem',
      props:['todo'],
      methods:{
        changeTo(id){
          this.$bus.$emit('changeTo',id)
        },
        deleteItem(id){
          this.$bus.$emit('deleteIt',id)
        },
        editTodoItem(todo){
          if(todo.isEdit in todo){
            todo.isEdit = true
          }else{
            this.$set(todo,'isEdit',true)
          }
          this.$nextTick(function (){
            this.$refs.inputTitle.focus()
          })

        },
        handleBlur(todo,event){
          //使输入框变回checkbox
          todo.isEdit=false

          if(!event.target.value.trim()){
            alert("修改不能为空")
            return
          }
          this.$bus.$emit('handleBl',todo.id,event.target.value)
        }
      }
    }
</script>

<style scoped>
/*item*/
li {
  list-style: none;
  height: 36px;
  line-height: 36px;
  padding: 0 5px;
  border-bottom: 1px solid #ddd;
}

li label {
  float: left;
  cursor: pointer;
}

li label li input {
  vertical-align: middle;
  margin-right: 6px;
  position: relative;
  top: -1px;
}

li button {
  float: right;
  /*鼠标光标未悬浮时隐藏*/
  display: none;
  margin-top: 3px;
}

li:before {
  content: initial;
}

li:last-child {
  border-bottom: none;
}

li:hover{
  background: #ddd;
  /*background: cyan;*/
}

li:hover button{
  /*鼠标光标悬浮时以块级元素显示*/
  display: block;
}

