<template>
  <div class="todo-footer" v-show="this.todos.length">
    <label>
      <input type="checkbox"
             :checked="allCheck"
             @click="changeAllCheck"
      />
    </label>
    <span>
       <span>已完成{{todosDone}}</span> / 全部{{todosLength}}
    </span>
    <button class="btn btn-danger" @click="clearDone">清除已完成任务</button>
  </div>
</template>


<script>
    export default {
      name:'MyFooter',
      props:['todos'],
      computed:{
          todosLength(){
            return this.todos.length
          },
          todosDone(){
            return this.todos.reduce((pre,todo) => pre + (todo.done ?  1 : 0),0)
          },
          allCheck(){
            return this.todosLength===this.todosDone&&this.todosLength>0
          }

      },
      methods:{
        changeAllCheck(e){
          this.$bus.$emit('changeAll',e.target.checked)
        },
        clearDone(){
          this.$bus.$emit('clearDo')
        }

      }
    }
</script>

<style scoped>
/*footer*/
.todo-footer {
  height: 40px;
  line-height: 40px;
  padding-left: 6px;
  margin-top: 5px;
}

.todo-footer label {
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}

.todo-footer label input {
  position: relative;
  top: -1px;
  vertical-align: middle;
  margin-right: 5px;
}

.todo-footer button {
  float: right;
  margin-top: 5px;
}
</style>